import React, { useState, useEffect } from 'react';
import { useAppSelector } from '../../app/hooks';
import { selectQuests } from '../../app/gameSlice';
import { QUESTS } from '../../Quests';
import styles from './QuestSystem.module.scss';

interface QuestItemProps {
  questState: any;
  questDefinition: any;
  onComplete: () => void;
}

const QuestItem: React.FC<QuestItemProps> = ({ questState, questDefinition, onComplete }) => {
  const [isCompleting, setIsCompleting] = useState(false);

  useEffect(() => {
    if (questState.isCompleted && !isCompleting) {
      setIsCompleting(true);
      // Через 5 секунд запускаем анимацию исчезновения
      setTimeout(() => {
        onComplete();
      }, 5000);
    }
  }, [questState.isCompleted, isCompleting, onComplete]);


  return (
    <div 
      className={`${styles.questItem} ${questState.isCompleted ? styles.completed : ''} ${isCompleting ? styles.completing : ''}`}
    >
      <div className={styles.questHeader}>
        <div className={styles.questTitle}>
          {questState.isCompleted && (
            <div className={styles.completedIcon}>✓</div>
          )}
          {questDefinition.name}
        </div>
      </div>
      <div className={styles.questContent}>
        <span className={styles.progressText}>- {Math.round(questState.progress)}/{questState.maxProgress}</span> {questDefinition.description}
      </div>
    </div>
  );
};

export const QuestSystem: React.FC = () => {
  const quests = useAppSelector(selectQuests);
  const [isCollapsed, setIsCollapsed] = useState(false);
  const [completedQuests, setCompletedQuests] = useState<Set<string>>(new Set());

  // Фильтруем видимые квесты, исключая уже удаленные
  const visibleQuests = quests.filter(quest => 
    quest.isVisible && !completedQuests.has(quest.id)
  );

  const handleQuestComplete = (questId: string) => {
    setCompletedQuests(prev => new Set([...Array.from(prev), questId]));
  };

  const toggleCollapse = () => {
    setIsCollapsed(!isCollapsed);
  };

  // Если нет видимых квестов, не показываем компонент
  if (visibleQuests.length === 0) {
    return null;
  }

  return (
    <div className={`${styles.questSystem} ${isCollapsed ? styles.collapsed : ''}`}>
      <div className={styles.questsHeader} onClick={toggleCollapse}>
        <div className={styles.questIcon}>
          {isCollapsed ? '📋' : '📝'}
        </div>
      </div>
      
      {!isCollapsed && (
        <div className={styles.questList}>
          {visibleQuests.map(questState => {
            const questDefinition = QUESTS[questState.id];
            if (!questDefinition) return null;
            
            return (
              <QuestItem
                key={questState.id}
                questState={questState}
                questDefinition={questDefinition}
                onComplete={() => handleQuestComplete(questState.id)}
              />
            );
          })}
        </div>
      )}
    </div>
  );
}; 