import React, { useState, useEffect, useCallback } from 'react';
import styles from './RestartCore.module.scss';
import classNames from 'classnames';

interface RestartCoreProps {
  onComplete: (success: boolean) => void;
}

// Типы паттернов для игры
type PatternType = 'line' | 'spiral' | 'zigzag';

// Интерфейс для панели
interface Panel {
  id: number;
  isActive: boolean;
  isDisabled: boolean;
  isStart: boolean;
  isFinish: boolean;
  isFalling: boolean; // Добавляем состояние падения
}

// Генерация паттерна активации панелей
const generatePattern = (type: PatternType): number[] => {
  // Массив индексов от 0 до 8 (9 панелей)
  const indices = Array.from({ length: 9 }, (_, i) => i);
  
  switch (type) {
    case 'line':
      // Линейный паттерн: слева направо, сверху вниз
      return [0, 1, 2, 5, 8, 7, 6, ];
    
    case 'spiral':
      // Спиральный паттерн: по часовой стрелке к центру
      return [0, 1, 4, 7, 8];
    
    case 'zigzag':
      // Зигзаг: змейкой слева направо
      return [0, 3, 4, 5, 8];
    
    default:
      // Случайный порядок по умолчанию
      return indices.sort(() => Math.random() - 0.5);
  }
};

const RestartCore: React.FC<RestartCoreProps> = ({ onComplete }) => {
  // Выбираем случайный тип паттерна
  const patternTypes: PatternType[] = ['line', 'spiral', 'zigzag'];
  const randomPatternType = patternTypes[Math.floor(Math.random() * patternTypes.length)];
  
  // Генерируем паттерн
  const [pattern] = useState<number[]>(generatePattern(randomPatternType));
  
  // Создаем состояние для панелей
  const createInitialPanels = (): Panel[] => Array.from({ length: 9 }, (_, i) => ({
    id: i,
    isActive: i === pattern[0], // Активируем первую панель в паттерне
    isDisabled: false,
    isStart: i === pattern[0], // Первая панель - стартовая
    isFinish: i === pattern[pattern.length - 1], // Последняя панель - финальная
    isFalling: false
  }));
  
  const [panels, setPanels] = useState<Panel[]>(createInitialPanels());
  const [activatedPanels, setActivatedPanels] = useState<Set<number>>(new Set([pattern[0]])); // Отслеживаем активированные панели
  const [timeLeft, setTimeLeft] = useState<number>(60); // 60 секунд на игру
  const [gameOver, setGameOver] = useState<boolean>(false);
  const [success, setSuccess] = useState<boolean>(false);
  const [failedPanels, setFailedPanels] = useState<number[]>([]);
  
  // Функция сброса панелей при ошибке
  const resetPanelsExceptFailed = (failedPanelId: number) => {
    setPanels(prev => prev.map(panel => {
      if (panel.id === failedPanelId || failedPanels.includes(panel.id)) {
        // Неверная панель начинает падать
        setFailedPanels(prev => [...prev, failedPanelId]);
        return { ...panel, isFalling: true };
      } else {
        // Остальные панели сбрасываются к исходному состоянию
        return {
          ...panel,
          isActive: panel.id === pattern[0], // Только стартовая остается активной
          isDisabled: false,
          isFalling: false
        };
      }
    }));
    
    // Сбрасываем активированные панели до стартовой
    setActivatedPanels(new Set([pattern[0]]));
    
    // Через некоторое время убираем падающую панель
    setTimeout(() => {
      setPanels(prev => prev.map(panel => 
        panel.id === failedPanelId 
          ? { ...panel, isDisabled: true, isFalling: false }
          : panel
      ));
    }, 500);
  };
  
  // Обработчик клика по панели
  const handlePanelClick = useCallback((panelId: number) => {
    if (gameOver) return;
    
    // Если панель уже активирована или отключена, игнорируем клик
    if (panels[panelId].isActive || panels[panelId].isDisabled || panels[panelId].isFalling) return;
    
    // Проверяем, есть ли панель в паттерне
    if (pattern.includes(panelId)) {
      // Правильный выбор - панель есть в паттерне
      const newPanels = [...panels];
      newPanels[panelId].isActive = true;
      setPanels(newPanels);
      
      // Добавляем панель к активированным
      const newActivatedPanels = new Set(activatedPanels);
      newActivatedPanels.add(panelId);
      setActivatedPanels(newActivatedPanels);
      
      // Проверяем, все ли панели паттерна активированы
      if (newActivatedPanels.size === pattern.length) {
        setSuccess(true);
        setGameOver(true);
        setTimeout(() => onComplete(true), 1500);
      }
    } else {
      // Неправильный выбор - панели нет в паттерне
      resetPanelsExceptFailed(panelId);
      
      // Отнимаем 10 секунд
      setTimeLeft(prev => Math.max(0, prev - 10));

    }
  }, [gameOver, panels, pattern, activatedPanels, onComplete]);
  
  // Эффект для таймера
  useEffect(() => {
    if (gameOver) return;
    
    const timer = setInterval(() => {
      setTimeLeft(prev => {
        if (prev <= 1) {
          // Время вышло
          setGameOver(true);
          setTimeout(() => onComplete(false), 1500);
          return 0;
        }
        return prev - 1;
      });
    }, 1000);
    
    return () => clearInterval(timer);
  }, [gameOver]);
  
  return (
    <div className={styles.restartCoreContainer}>
      <div className={styles.gameHeader}>
        <h3>Перезапуск ядра</h3>
        <div className={styles.timer}>
          Осталось времени: <span className={timeLeft < 15 ? styles.timerWarning : ''}>{timeLeft}s</span>
        </div>
      </div>
      <div className={styles.panelsGrid}>
        {panels.map(panel => (
          <div
            key={panel.id}
            className={classNames(
              styles.panel,
              {
                [styles.active]: panel.isActive,
                [styles.disabled]: panel.isDisabled,
                [styles.start]: panel.isStart,
                [styles.finish]: panel.isFinish,
                [styles.falling]: panel.isFalling
              }
            )}
            onClick={() => handlePanelClick(panel.id)}
          >
            {panel.isStart && <div className={styles.startMarker}>Старт</div>}
            {panel.isFinish && <div className={styles.finishMarker}>Цель</div>}
          </div>
        ))}
      </div>
      
      
      <div className={styles.progress}>
        Активировано: {activatedPanels.size} / {pattern.length}
      </div>
      
      {gameOver && (
        <div className={`${styles.gameOverMessage} ${success ? styles.success : styles.failure}`}>
          {success ? 'Ядро перезапущено!' : 'ИИ перезагрузился, он сам запустит ядро!'}
        </div>
      )}
    </div>
  );
};

export default RestartCore; 