import React, { useState } from 'react';
import styles from './index.module.scss';
import { InstructionPopup } from '../../components/InstructionPopup';

interface StartScreenProps {
  onResume: () => void;
  onNewGame: () => void;
  hasSave: boolean;
}

const StartScreen: React.FC<StartScreenProps> = ({ onResume, onNewGame, hasSave }) => {
  const [showInstructions, setShowInstructions] = useState(false);
  const [isButtonJumping, setIsButtonJumping] = useState(false);

  const handleShowInstructions = () => {
    setShowInstructions(true);
  };

  const handleCloseInstructions = () => {
    setShowInstructions(false);
  };

  return (
    <div className={styles.startScreen}>
      <h1>ClickInc.</h1>
      <div className={styles.startButtons}>
        <button disabled={!hasSave} onClick={onResume}>Продолжить</button>
        <button onClick={onNewGame}>Новая игра</button>
        <button onClick={handleShowInstructions} className={styles.howToPlayButton}>
          Как играть
        </button>
      </div>
      
      {showInstructions && (
        <InstructionPopup 
          onClose={handleCloseInstructions} 
          isButtonJumping={isButtonJumping}
          setIsButtonJumping={setIsButtonJumping}
        />
      )}
    </div>
  );
};

export default StartScreen; 