import React, { useState, useEffect, useCallback } from 'react';
import styles from './HackAirlock.module.scss';
import classNames from 'classnames';

interface HackAirlockProps {
  onComplete: (success: boolean) => void;
}

// Доступные символы для последовательности
const SYMBOLS = ['♣', '♦', '♥', '♠', '★', '◆', '●', '▲', '■', '🤡'];

// Генерация случайной последовательности
const generateSequence = (): string[] => {
  const length = Math.floor(Math.random() * 3) + 4; // 4-6 символов
  const sequence: string[] = [];
  
  for (let i = 0; i < length; i++) {
    const randomSymbol = SYMBOLS[Math.floor(Math.random() * SYMBOLS.length)];
    sequence.push(randomSymbol);
  }
  
  return sequence;
};

// Генерация подсказок (позиции символов в окружении)
const generateHints = (sequence: string[]): { symbol: string; position: string; order: number }[] => {
  const positions = ['floor', 'ceiling', 'leftWall', 'rightWall', 'console'];
  
  return sequence.map((symbol, index) => ({
    symbol,
    position: positions[index % positions.length],
    order: index + 1
  }));
};

const HackAirlock: React.FC<HackAirlockProps> = ({ onComplete }) => {
  // Генерируем секретную последовательность и подсказки
  const [secretSequence] = useState<string[]>(generateSequence());
  const [hints] = useState<{ symbol: string; position: string; order: number }[]>(
    generateHints(secretSequence)
  );
  
  // Состояние игры
  const [showStartScreen, setShowStartScreen] = useState<boolean>(true);
  const [startScreenAnimating, setStartScreenAnimating] = useState<boolean>(false);
  const [inputSequence, setInputSequence] = useState<string[]>([]);
  const [timeLeft, setTimeLeft] = useState<number>(180); // 180 секунд
  const [timerSpeed, setTimerSpeed] = useState<number>(1000); // Скорость таймера в мс
  const [gameOver, setGameOver] = useState<boolean>(false);
  const [success, setSuccess] = useState<boolean>(false);
  const [errorMessage, setErrorMessage] = useState<string>('');
  
  // Обработчик клика по символу
  const handleSymbolClick = useCallback((symbol: string) => {
    if (gameOver) return;
    
    const newSequence = [...inputSequence, symbol];
    const currentIndex = newSequence.length - 1;
    
    // Проверяем, правильный ли символ на текущей позиции
    if (newSequence[currentIndex] === secretSequence[currentIndex]) {
      // Правильный символ
      setInputSequence(newSequence);
      setErrorMessage('');
      
      // Проверяем, завершена ли последовательность
      if (newSequence.length === secretSequence.length) {
        setSuccess(true);
        setGameOver(true);
        setTimeout(() => onComplete(true), 1500);
      }
    } else {
      // Неправильный символ
      setInputSequence([]); // Сбрасываем последовательность
      setErrorMessage('Неверный символ! Последовательность сброшена.');
      
      // Ускоряем таймер на 15%
      setTimerSpeed(prev => Math.max(200, prev * 0.85));
      
      // Убираем сообщение об ошибке через 2 секунды
      setTimeout(() => setErrorMessage(''), 2000);
    }
  }, [gameOver, inputSequence, secretSequence, onComplete]);
  
  // Эффект для таймера с переменной скоростью
  useEffect(() => {
    if (gameOver) return;
    
    const timer = setInterval(() => {
      setTimeLeft(prev => {
        if (prev <= 1) {
          setGameOver(true);
          setTimeout(() => onComplete(false), 1500);
          return 0;
        }
        return prev - 1;
      });
    }, timerSpeed);
    
    return () => clearInterval(timer);
  }, [gameOver, timerSpeed]);
  

  // Обработчик запуска игры
  const handleStartGame = () => {
    setStartScreenAnimating(true);
    // Скрываем стартовый экран после завершения анимации
    setTimeout(() => {
      setShowStartScreen(false);
    }, 800);
  };
  
  return (
    <div className={styles.hackAirlockContainer}>
      {/* Стартовый экран */}
      {showStartScreen && (
        <div className={`${styles.startScreen} ${startScreenAnimating ? styles.slideUpAnimation : ''}`}>
          <div className={styles.startContent}>
            <h2>Экстренная ситуация!</h2>
            <div className={styles.storyText}>
              <p>Зальцман запер себя в грузовом отсеке и не выходит уже 10 минут!</p>
              <p>Он там все пожрет! Нужно взломать шлюз и остановить его!</p>
            </div>
            <button 
              className={styles.startButton}
              onClick={handleStartGame}
            >
              Взломать шлюз
            </button>
          </div>
        </div>
      )}

      {/* Фоновые подсказки */}
      {!showStartScreen && (
        <div className={styles.backgroundHints}>
          {secretSequence.map((symbol, index) => (
            <div 
              key={index}
              className={styles.backgroundHint}
              style={{
                left: `${15 + (index * 12)}%`,
                top: `${20 + (index % 3) * 25}%`,
                animationDelay: `${index * 0.5}s`
              }}
            >
              {symbol}
            </div>
          ))}
        </div>
      )}

      <div className={styles.gameHeader}>
        <h3>Взлом шлюзов</h3>
        <div className={styles.timer}>
          Времени: <span className={timeLeft < 15 ? styles.timerWarning : ''}>{timeLeft}s</span>
          {timerSpeed < 1000 && <span className={styles.speedWarning}> ⚡ ускорен</span>}
        </div>
      </div>
      
      <div className={styles.consoleB}>
        <div className={styles.screen}>
          <div className={styles.sequenceDisplay}>
            <span className={styles.prompt}>Последовательность: </span>
            <span className={styles.sequence}>
              {inputSequence.join(' ')}
              <span className={styles.cursor}>|</span>
            </span>
          </div>
          <div className={styles.statusLine}>
            Прогресс: {inputSequence.length} / {secretSequence.length}
          </div>
        </div>
        
        {errorMessage && (
          <div className={styles.errorMessage}>
            {errorMessage}
          </div>
        )}
      </div>
      
      <div className={styles.symbolsGrid}>
        {SYMBOLS.map((symbol, index) => (
          <button
            key={index}
            className={styles.symbolButton}
            onClick={() => handleSymbolClick(symbol)}
            disabled={gameOver}
          >
            {symbol}
          </button>
        ))}
      </div>
      
      
      
      {gameOver && (
        <div className={`${styles.gameOverMessage} ${success ? styles.success : styles.failure}`}>
          {success ? 'Шлюз открыт! Доступ получен!' : 'Система заблокирована. Доступ запрещен.'}
        </div>
      )}
    </div>
  );
};

export default HackAirlock; 