import React, { useRef } from "react";
import { useTransition, animated } from '@react-spring/web';
import { useAppDispatch, useAppSelector } from "../../app/hooks";
import { CoinEffect, removeCoinEffect, selectCoinEffects } from "../../app/visualEffectsSlice";
import styles from './CoinEffects.module.scss';

export const CoinEffects: React.FC = () => {
  const dispatch = useAppDispatch();
  const coins = useAppSelector(selectCoinEffects);
  const containerRef = useRef<HTMLDivElement>(null);

  const transitions = useTransition(coins, {
    from: { opacity: 1, transform: 'translateY(0px)' },
    enter: { opacity: 0, transform: 'translateY(-50px)' },
    config: { duration: 1000 },
    keys: item => item.id,
    onRest: (_result: any, _ctrl: any, item: CoinEffect) => {
      dispatch(removeCoinEffect(item.id));
    },
  });

  // Функция для форматирования значения
  const formatValue = (item: CoinEffect) => {
    let displayValue = item.value;
    
    // Если числовое значение, округляем его
    if (typeof item.value === 'number') {
      displayValue = Math.round(item.value);
    }
    
    return `${item.prefix || ''}${displayValue}${item.suffix || ''}`;
  };

  // Функция для проверки, является ли строка HTML
  const isHtmlString = (str: string): boolean => {
    return /<[^>]*>/g.test(str);
  };

  // Функция для безопасного рендеринга HTML или текста
  const renderValue = (item: CoinEffect) => {
    // Проверяем, содержит ли исходное значение HTML теги
    if (typeof item.value === 'string' && isHtmlString(item.value)) {
      // Если само значение является HTML, добавляем prefix и suffix снаружи
      const prefixElement = item.prefix ? <span>{item.prefix}</span> : null;
      const suffixElement = item.suffix ? <span>{item.suffix}</span> : null;
      
      return (
        <>
          {prefixElement}
          <span dangerouslySetInnerHTML={{ __html: item.value }} />
          {suffixElement}
        </>
      );
    }
    
    // Иначе обрабатываем как обычно
    const formattedValue = formatValue(item);
    
    // Проверяем результат форматирования на наличие HTML
    if (typeof formattedValue === 'string' && isHtmlString(formattedValue)) {
      return (
        <span dangerouslySetInnerHTML={{ __html: formattedValue }} />
      );
    }
    
    // Иначе рендерим как обычный текст
    return formattedValue;
  };

  return (
    <div ref={containerRef} className={styles.coinsContainer}>
      {transitions((style, item) => (
        <animated.div
          style={{
            ...style,
            position: 'absolute',
            left: `${item.position.x}px`,
            top: `${item.position.y}px`,
            transform: style.transform,
            pointerEvents: 'none',
            fontSize: '16px',
            color: '#FFD700',
            fontWeight: 'bold',
            textShadow: '0 0 3px rgba(0,0,0,0.5)',
            zIndex: 1000,
          }}
        >
          {renderValue(item)}
        </animated.div>
      ))}
    </div>
  );
};