import { Quest, GameState } from './types';
import { ECONOMY, FAITH, SCIENCE } from './app/constants';

export const QUESTS: Record<string, Quest> = {
  first_upgrade: {
    id: 'first_upgrade',
    name: 'Приобрести апгрейд автодобычи',
    description: 'Накопите 10 ресурсов и купите первое улучшение',
    progress: 0,
    maxProgress: 10,
    isCompleted: false,
    isVisible: true,
    checkProgress: (state: GameState) => {
      // Прогресс основан на общем количестве ресурсов, но ограничен 10
      return Math.min(state.total, 10);
    },
    checkVisibility: (state: GameState) => {
      // Квест видим сразу после выбора страны
      return !!state.selectedCountry && state.stage >= 1;
    },
    checkCompletion: (state: GameState) => {
      // Квест завершен, если куплен любой апгрейд
      return state.upgrades.some(upgrade => upgrade.level > 0);
    }
  },

  develop_sphere: {
    id: 'develop_sphere',
    name: 'Начать развивать одну из сфер',
    description: 'Накопите 10 очков в одной из трех сфер развития',
    progress: 0,
    maxProgress: 10,
    isCompleted: false,
    isVisible: false,
    checkProgress: (state: GameState) => {
      // Прогресс - максимальное количество ресурсов среди всех сфер
      const maxSphereResources = Math.max(
        state.spheres[ECONOMY].resources,
        state.spheres[FAITH].resources,
        state.spheres[SCIENCE].resources
      );
      return Math.min(maxSphereResources, 10);
    },
    checkVisibility: (state: GameState) => {
      // Квест становится видимым когда общие ресурсы превышают 100
      return state.total > 100;
    },
    checkCompletion: (state: GameState) => {
      // Квест завершен, если в любой сфере больше 10 ресурсов
      return state.spheres[ECONOMY].resources > 10 ||
             state.spheres[FAITH].resources > 10 ||
             state.spheres[SCIENCE].resources > 10;
    }
  },

  expand_borders: {
    id: 'expand_borders',
    name: 'Расширение границ',
    description: 'Захватите первую страну',
    progress: 0,
    maxProgress: 1,
    isCompleted: false,
    isVisible: false,
    checkProgress: (state: GameState) => {
      // Прогресс 0 если захвачена только выбранная страна, 1 если больше
      return state.conqueredCountries.length > 1 ? 1 : 0;
    },
    checkVisibility: (state: GameState) => {
      // Квест становится видимым когда доступна хотя бы одна страна для захвата
      // Это происходит когда есть события на карте или достаточно ресурсов
      return state.mapEvents.length > 0 || state.total > 500;
    },
    checkCompletion: (state: GameState) => {
      // Квест завершен, если захвачено больше одной страны
      return state.conqueredCountries.length > 1;
    }
  },

  ship_shields: {
    id: 'ship_shields',
    name: 'Постройка щитов корабля',
    description: 'Постройте защитные щиты для космического корабля',
    progress: 0,
    maxProgress: 1,
    isCompleted: false,
    isVisible: false,
    checkProgress: (state: GameState) => {
      // Прогресс 1 если щиты корабля построены
      return state.cosmicCivilization.shipShield ? 1 : 0;
    },
    checkVisibility: (state: GameState) => {
      // Квест становится видимым после перехода на 2й этап
      return state.stage >= 2;
    },
    checkCompletion: (state: GameState) => {
      // Квест завершен, если в secondStage есть ключ shipShield
      return state.secondStage.includes('shipShield') || state.cosmicCivilization.shipShield;
    }
  }
};

// Функция для получения всех квестов в виде массива
export const getAllQuests = (): Quest[] => {
  return Object.values(QUESTS);
};

// Функция для получения квеста по ID
export const getQuestById = (id: string): Quest | undefined => {
  return QUESTS[id];
}; 