import React from 'react';
import { useAppDispatch, useAppSelector } from '../../app/hooks';
import { selectShip, selectSpheres, toggleShield } from '../../app/gameSlice';
import { SCIENCE, FAITH, ECONOMY } from '../../app/constants';
import styles from './ShieldControls.module.scss';

const sphereNames = {
  [SCIENCE]: 'Щит науки',
  [FAITH]: 'Щит веры',
  [ECONOMY]: 'Щит экономики'
};

const ShieldControls: React.FC = () => {
  const dispatch = useAppDispatch();
  const ship = useAppSelector(selectShip);
  const spheres = useAppSelector(selectSpheres);
  
  const handleToggleShield = (sphere: string) => {
    dispatch(toggleShield({ sphere, active: !ship.activeShields[sphere] }));
  };
  
  return (
    <div className={styles.shieldControls}>
      {Object.keys(spheres).map(sphere => (
        <div 
          key={sphere}
          className={`${styles.shieldButton} ${ship.activeShields[sphere] ? styles.active : ''}`}
          onClick={() => handleToggleShield(sphere)}
        >
          <div className={styles.icon}>{ship.activeShields[sphere] ? '🛡' : spheres[sphere as keyof typeof spheres].icon}</div>
          <div className={styles.name}>{sphereNames[sphere as keyof typeof sphereNames]}</div>
        </div>
      ))}
    </div>
  );
};

export default ShieldControls; 