import React, { useState, useEffect } from 'react';
import Ship, { ShipSection, MiniGame } from './Ship';
import { useAppDispatch, useAppSelector } from '../../../app/hooks';
import { selectGame, applyMiniGameEffect } from '../../../app/gameSlice';
import styles from './ShipManager.module.scss';
import { ExtendedMiniGame } from '../../../MiniGames';

// Импортируем мини-игры
import RestartCore from './games/RestartCore';
import HackAirlock from './games/HackAirlock';
import ExoticSynthesis from './games/ExoticSynthesis';
import AlienCodeBreaker from './games/AlienCodeBreaker';

// В будущем здесь будут импортированы компоненты мини-игр
// import ReactorStabilizationGame from './games/ReactorStabilization';
// import PowerDistributionGame from './games/PowerDistribution';
// и т.д.

interface ShipManagerProps {
  progress?: number; // Прогресс полета от 0 до 100
}

const ShipManager: React.FC<ShipManagerProps> = ({ progress: externalProgress }) => {
  const dispatch = useAppDispatch();
  const game = useAppSelector(selectGame);
  
  // Состояние для прогресса корабля
  const [progress, setProgress] = useState(externalProgress || 0);
  
  // Состояние для текущей активной мини-игры
  const [activeGame, setActiveGame] = useState<ExtendedMiniGame | null>(null);
  
  // Состояние для результата мини-игры
  const [gameResult, setGameResult] = useState<'success' | 'failure' | null>(null);
  
  useEffect(() => {
    // Если передан внешний прогресс, используем его
    if (externalProgress !== undefined) {
      setProgress(externalProgress);
      return;
    }
    
    // Иначе обновляем прогресс корабля на основе времени полета
    if (game.ship && game.ship.flightStartTime > 0) {
      const now = Date.now();
      const flightDuration = now - game.ship.flightStartTime;
      
      // Предполагаем, что полет занимает 5 минут (300 секунд)
      const totalFlightTime = 300 * 1000; // в миллисекундах
      
      // Вычисляем прогресс в процентах
      const calculatedProgress = Math.min(100, (flightDuration / totalFlightTime) * 100);
      setProgress(calculatedProgress);
    }
  }, [game.ship, externalProgress]);
  
  // Обработчик для запуска мини-игры напрямую
  const handleGameStart = (selectedGame: ExtendedMiniGame) => {
    setActiveGame(selectedGame);
  };
  
  // Обработчик для завершения мини-игры
  const handleGameComplete = (success: boolean) => {
    if (!activeGame) return;
    
    setGameResult(success ? 'success' : 'failure');
    
    // Применяем эффект мини-игры через редукс
    const { id, resourceEffect } = activeGame;
    const { type, successMultiplier, failureMultiplier, duration } = resourceEffect;
    
    dispatch(applyMiniGameEffect({
      gameId: id,
      success,
      resourceType: type,
      multiplier: success ? successMultiplier : failureMultiplier,
      duration
    }));
    
    // Очищаем активную игру через 3 секунды (чтобы показать результат)
    setTimeout(() => {
      setActiveGame(null);
      setGameResult(null);
    }, 3000);
  };
  
  // Функция для рендеринга конкретной мини-игры по ID
  const renderGame = () => {
    if (!activeGame) return null;
    
    switch (activeGame.id) {
      case 'restart_core':
        return <RestartCore onComplete={handleGameComplete} />;
      
      case 'hack_airlock':
        return <HackAirlock onComplete={handleGameComplete} />;
      
      case 'exotic_synthesis':
        return <ExoticSynthesis onComplete={handleGameComplete} />;
      
      case 'alien_code_breaker':
        return <AlienCodeBreaker onComplete={handleGameComplete} />;
      
      case 'scan_systems':
        return (
          <div className={styles.defaultGameInterface}>
            <h3>{activeGame.name}</h3>
            <p>Эта игра ещё не реализована</p>
            <button onClick={() => handleGameComplete(Math.random() > 0.5)}>
              Симулировать результат
            </button>
          </div>
        );
      
      case 'tactical_analysis':
        return (
          <div className={styles.defaultGameInterface}>
            <h3>{activeGame.name}</h3>
            <p>Эта игра ещё не реализована</p>
            <button onClick={() => handleGameComplete(Math.random() > 0.5)}>
              Симулировать результат
            </button>
          </div>
        );
      
      case 'resource_optimization':
        return (
          <div className={styles.defaultGameInterface}>
            <h3>{activeGame.name}</h3>
            <p>Эта игра ещё не реализована</p>
            <button onClick={() => handleGameComplete(Math.random() > 0.5)}>
              Симулировать результат
            </button>
          </div>
        );
      
      default:
        // Временный интерфейс для демонстрации активной мини-игры и результата
        return (
          <div className={styles.defaultGameInterface}>
            <h3>{activeGame.name}</h3>
            {gameResult ? (
              <div className={`${styles.gameResult} ${gameResult === 'success' ? styles.success : styles.failure}`}>
                {gameResult === 'success' ? 'Успех!' : 'Провал!'}
                <div className={styles.effectInfo}>
                  <div className={styles.effectType}>
                    {activeGame.resourceEffect.type === 'global' ? 'Все ресурсы' : 
                      activeGame.resourceEffect.type === 'economy' ? 'Экономика' :
                      activeGame.resourceEffect.type === 'science' ? 'Наука' : 
                      activeGame.resourceEffect.type === 'faith' ? 'Вера' : 'Щиты'}:
                  </div>
                  <div className={styles.effectValue}>
                    {gameResult === 'success' 
                      ? `+${((activeGame.resourceEffect.successMultiplier - 1) * 100).toFixed(0)}%` 
                      : `${((activeGame.resourceEffect.failureMultiplier - 1) * 100).toFixed(0)}%`}
                  </div>
                  <div className={styles.effectDuration}>
                    на {activeGame.resourceEffect.duration} секунд
                  </div>
                </div>
              </div>
            ) : (
              <div className={styles.gameInProgress}>
                Мини-игра в процессе...
                <div className={styles.loadingSpinner}></div>
              </div>
            )}
          </div>
        );
    }
  };
  
  return (
    <div className={styles.shipManagerContainer}>
      {/* Отображаем корабль с текущим прогрессом */}
      <Ship 
        progress={progress} 
        onGameStart={handleGameStart}
      />
      
      {/* Рендеринг активной мини-игры */}
      {activeGame && (
        <div className={styles.activeGameOverlay}>
          <div className={styles.activeGameContainer}>
            <button 
              className={styles.closeButton}
              onClick={() => setActiveGame(null)}
            >
              ×
            </button>
            {renderGame()}
          </div>
        </div>
      )}
    </div>
  );
};

export default ShipManager; 