import React, { useState, useEffect, useCallback } from 'react';
import styles from './ExoticSynthesis.module.scss';
import classNames from 'classnames';

interface ExoticSynthesisProps {
  onComplete: (success: boolean) => void;
}

// Типы частиц
type ParticleType = 'proton' | 'neutron' | 'electron';

interface Particle {
  id: string;
  type: ParticleType;
  charge: number;
  mass: number;
  x: number;
  y: number;
  vx: number;
  vy: number;
}

// Свойства частиц
const PARTICLE_PROPERTIES = {
  proton: { charge: 1, mass: 1.2, symbol: '+', color: '#ff4444', name: 'Протон' },
  neutron: { charge: 0, mass: 1.5, symbol: 'o', color: '#888888', name: 'Нейтрон' },
  electron: { charge: -1, mass: 0.1, symbol: '-', color: '#4444ff', name: 'Электрон' }
};

// Предустановленные целевые комбинации (стабильные элементы)
const TARGET_COMBINATIONS = [
  { protons: 2, neutrons: 2, electrons: 2, name: 'Гелий-4' },
  { protons: 3, neutrons: 4, electrons: 3, name: 'Литий-7' },
  { protons: 4, neutrons: 5, electrons: 4, name: 'Бериллий-9' },
  { protons: 5, neutrons: 6, electrons: 5, name: 'Бор-11' },
  { protons: 6, neutrons: 6, electrons: 6, name: 'Углерод-12' }
];

// Проверка стабильности элемента
const checkStability = (particles: Particle[], target: typeof TARGET_COMBINATIONS[0]): { stable: boolean; message: string } => {
  const counts = { proton: 0, neutron: 0, electron: 0 };
  let totalCharge = 0;
  let protonMass = 0;
  let otherMass = 0;

  particles.forEach(particle => {
    counts[particle.type]++;
    totalCharge += particle.charge;
    if (particle.type === 'proton') {
      protonMass += particle.mass;
    } else {
      otherMass += particle.mass;
    }
  });

  // Проверка заряда
  if (totalCharge !== 0) {
    return { stable: false, message: `Заряд не сбалансирован: ${totalCharge > 0 ? '+' : ''}${totalCharge}` };
  }

  // Проверка массового баланса
  if (protonMass > otherMass + 2) {
    return { stable: false, message: 'Протоны слишком тяжелые для стабильности' };
  }

  // Проверка соответствия целевой комбинации
  if (counts.proton === target.protons && counts.neutron === target.neutrons && counts.electron === target.electrons) {
    return { stable: true, message: `Успешно синтезирован ${target.name}!` };
  }

  // Если баланс хороший, но комбинация не целевая
  if (counts.proton < target.protons || counts.neutron < target.neutrons || counts.electron < target.electrons) {
    return { stable: false, message: 'Добавьте еще частиц для завершения синтеза' };
  }

  return { stable: false, message: 'Превышено количество частиц' };
};

const ExoticSynthesis: React.FC<ExoticSynthesisProps> = ({ onComplete }) => {
  // Выбираем случайную целевую комбинацию
  const [targetCombination] = useState(() => 
    TARGET_COMBINATIONS[Math.floor(Math.random() * TARGET_COMBINATIONS.length)]
  );
  
  // Состояние игры
  const [particles, setParticles] = useState<Particle[]>([]);
  const [timeLeft, setTimeLeft] = useState<number>(120); // 2 минуты
  const [gameOver, setGameOver] = useState<boolean>(false);
  const [success, setSuccess] = useState<boolean>(false);
  const [stabilityMessage, setStabilityMessage] = useState<string>('');
  const [isExploding, setIsExploding] = useState<boolean>(false);
  const [isUnstable, setIsUnstable] = useState<boolean>(false);
  const [instabilityTimer, setInstabilityTimer] = useState<NodeJS.Timeout | null>(null);
  const [instabilityTimeLeft, setInstabilityTimeLeft] = useState<number>(0);
  
  // Генерация уникального ID для частицы
  const generateParticleId = () => Math.random().toString(36).substr(2, 9);
  
  // Добавление частицы в реактор
  const addParticle = useCallback((type: ParticleType) => {
    if (gameOver || isExploding) return;
    
    const properties = PARTICLE_PROPERTIES[type];
    const newParticle: Particle = {
      id: generateParticleId(),
      type,
      charge: properties.charge,
      mass: properties.mass,
      x: 50 + (Math.random() - 0.5) * 20, // Центр колбы с небольшим разбросом
      y: 70 + (Math.random() - 0.5) * 20,
      vx: (Math.random() - 0.5) * 0.5, // Случайная скорость
      vy: (Math.random() - 0.5) * 0.5
    };
    
    const newParticles = [...particles, newParticle];
    setParticles(newParticles);
    
    // Проверка стабильности
    const stability = checkStability(newParticles, targetCombination);
    setStabilityMessage(stability.message);
    
    if (stability.stable) {
      // Успешный синтез - отменяем нестабильность
      clearInstability();
      setSuccess(true);
      setGameOver(true);
      setTimeout(() => onComplete(true), 2000);
    } else if (stability.message.includes('Заряд не сбалансирован') || 
               stability.message.includes('слишком тяжелые') ||
               stability.message.includes('Превышено')) {
      // Критический дисбаланс - запускаем таймер нестабильности
      startInstability();
    } else {
      // Некритичное состояние - отменяем нестабильность
      clearInstability();
    }
  }, [gameOver, isExploding, particles, targetCombination, onComplete]);
  
  // Запуск нестабильности
  const startInstability = () => {
    if (isUnstable) return; // Уже нестабильна
    
    setIsUnstable(true);
    setInstabilityTimeLeft(4); // 4 секунды на стабилизацию
    
    const timer = setInterval(() => {
      setInstabilityTimeLeft(prev => {
        if (prev <= 1) {
          // Время вышло - взрыв!
          clearInterval(timer);
          setInstabilityTimer(null);
          triggerExplosion();
          return 0;
        }
        return prev - 1;
      });
    }, 1000);
    
    setInstabilityTimer(timer);
  };

  // Отмена нестабильности
  const clearInstability = () => {
    if (instabilityTimer) {
      clearInterval(instabilityTimer);
      setInstabilityTimer(null);
    }
    setIsUnstable(false);
    setInstabilityTimeLeft(0);
  };
  
  // Взрыв частиц
  const triggerExplosion = () => {
    clearInstability(); // Очищаем таймер нестабильности
    setIsExploding(true);
    
    // Разбрасываем частицы
    setParticles(prevParticles => 
      prevParticles.map(particle => ({
        ...particle,
        vx: (Math.random() - 0.5) * 8,
        vy: (Math.random() - 0.5) * 8
      }))
    );
    
    // Очищаем реактор через секунду
    setTimeout(() => {
      setParticles([]);
      setIsExploding(false);
      setStabilityMessage('');
    }, 1000);
  };
  
  // Анимация частиц (магнитное поле)
  useEffect(() => {
    if (gameOver || isExploding) return;
    
    const animationInterval = setInterval(() => {
      setParticles(prevParticles => 
        prevParticles.map(particle => {
          let { x, y, vx, vy } = particle;
          
          // Магнитное поле - частицы притягиваются к центру и слегка покачиваются
          const centerX = 50;
          const centerY = 50;
          const dx = centerX - x;
          const dy = centerY - y;
          const distance = Math.sqrt(dx * dx + dy * dy);
          
          if (distance > 0) {
            // Притяжение к центру
            vx += (dx / distance) * 0.02;
            vy += (dy / distance) * 0.02;
          }
          
          // Добавляем случайное движение (магнитные флуктуации)
          vx += (Math.random() - 0.5) * 0.1;
          vy += (Math.random() - 0.5) * 0.1;
          
          // Ограничиваем скорость
          const speed = Math.sqrt(vx * vx + vy * vy);
          if (speed > 1) {
            vx = (vx / speed) * 1;
            vy = (vy / speed) * 1;
          }
          
          // Обновляем позицию
          x += vx;
          y += vy;
          
          // Ограничиваем движение колбой
          if (x < 20) { x = 20; vx = Math.abs(vx); }
          if (x > 80) { x = 80; vx = -Math.abs(vx); }
          if (y < 20) { y = 20; vy = Math.abs(vy); }
          if (y > 80) { y = 80; vy = -Math.abs(vy); }
          
          return { ...particle, x, y, vx, vy };
        })
      );
    }, 50);
    
    return () => clearInterval(animationInterval);
  }, [gameOver, isExploding]);
  
  // Таймер игры
  useEffect(() => {
    if (gameOver) return;
    
    const timer = setInterval(() => {
      setTimeLeft(prev => {
        if (prev <= 1) {
          setGameOver(true);
          setTimeout(() => onComplete(false), 1500);
          return 0;
        }
        return prev - 1;
      });
    }, 1000);
    
    return () => clearInterval(timer);
  }, [gameOver]);
  
  // Подсчет текущих частиц
  const currentCounts = particles.reduce((acc, particle) => {
    acc[particle.type]++;
    return acc;
  }, { proton: 0, neutron: 0, electron: 0 });
  
  // Очистка таймеров при размонтировании
  useEffect(() => {
    return () => {
      if (instabilityTimer) {
        clearInterval(instabilityTimer);
      }
    };
  }, [instabilityTimer]);
  
  return (
    <div className={styles.exoticSynthesisContainer}>
      <div className={styles.gameHeader}>
        <h3>Синтез экзотической материи</h3>
        <div className={styles.timer}>
          Время: <span className={timeLeft < 30 ? styles.timerWarning : ''}>{timeLeft}s</span>
        </div>
      </div>
      
      <div className={styles.targetInfo}>
        <h4>Цель: {targetCombination.name}</h4>
        <div className={styles.targetComposition}>
          <span>Протоны: {targetCombination.protons}</span>
          <span>Нейтроны: {targetCombination.neutrons}</span>
          <span>Электроны: {targetCombination.electrons}</span>
        </div>
      </div>
      
      <div className={styles.reactorContainer}>
        <div className={classNames(styles.reactor, { 
          [styles.exploding]: isExploding,
          [styles.unstable]: isUnstable 
        })}>
          <div className={styles.magneticField}></div>
          {particles.map(particle => (
            <div
              key={particle.id}
              className={classNames(styles.particle, styles[particle.type])}
              style={{
                left: `${particle.x}%`,
                top: `${particle.y}%`
              }}
            >
              {PARTICLE_PROPERTIES[particle.type].symbol}
            </div>
          ))}
        </div>
        
        {isUnstable && (
          <div className={styles.instabilityWarning}>
            ⚠️ СИСТЕМА НЕСТАБИЛЬНА! ⚠️
            <div className={styles.countdown}>
              Взрыв через: {instabilityTimeLeft}с
            </div>
          </div>
        )}
        
        <div className={styles.currentCounts}>
          <div>Протоны: {currentCounts.proton}/{targetCombination.protons}</div>
          <div>Нейтроны: {currentCounts.neutron}/{targetCombination.neutrons}</div>
          <div>Электроны: {currentCounts.electron}/{targetCombination.electrons}</div>
        </div>
      </div>
      
      <div className={styles.controls}>
        {Object.entries(PARTICLE_PROPERTIES).map(([type, props]) => (
          <button
            key={type}
            className={classNames(styles.particleButton, styles[type])}
            onClick={() => addParticle(type as ParticleType)}
            disabled={gameOver}
          >
            <div className={styles.buttonSymbol}>{props.symbol}</div>
            <div className={styles.buttonName}>{props.name}</div>
            <div className={styles.buttonStats}>
              Заряд: {props.charge > 0 ? '+' : ''}{props.charge}<br/>
              Масса: {props.mass}
            </div>
          </button>
        ))}
      </div>
      
      {stabilityMessage && (
        <div className={classNames(styles.stabilityMessage, {
          [styles.success]: success,
          [styles.error]: stabilityMessage.includes('не сбалансирован') || 
                          stabilityMessage.includes('слишком тяжелые') ||
                          stabilityMessage.includes('Превышено')
        })}>
          {stabilityMessage}
        </div>
      )}
      
      {gameOver && (
        <div className={`${styles.gameOverMessage} ${success ? styles.success : styles.failure}`}>
          {success ? 'Синтез завершен! Элемент стабилен!' : 'Время вышло! Реактор отключается.'}
        </div>
      )}
    </div>
  );
};

export default ExoticSynthesis; 