import React from "react";
import { useSpring } from "@react-spring/web";
import { animated } from "@react-spring/web";
import styles from "./InstructionPopup.module.scss";
import { useAppSelector } from "../../app/hooks";
import { selectedCountry } from "../../app/gameSlice";
import click from "../../assets/click.jpg";
import upgrade from "../../assets/upg.jpg";
import talent from "../../assets/talant.jpg";
import project from "../../assets/proj.jpg";
import problem from "../../assets/problem.jpg";
import conquer from "../../assets/conq.jpg";

interface InstructionPopupProps {
  onClose: () => void;
  isButtonJumping: boolean;
  setIsButtonJumping: (value: boolean) => void;
}

// Компонент попапа с инструкциями
export const InstructionPopup: React.FC<InstructionPopupProps> = ({ 
  onClose, 
  isButtonJumping, 
  setIsButtonJumping 
}) => {
  const country = useAppSelector(selectedCountry);
  
  // Анимация для кнопки OK
  const buttonAnimation = useSpring({
    transform: isButtonJumping ? 'translateY(-15px)' : 'translateY(0px)',
    config: { tension: 1000, friction: 5 }
  });

  // Обработчик клика на бэкдроп
  const handleBackdropClick = (e: React.MouseEvent) => {
    // Предотвращаем закрытие при клике на бэкдроп
    e.stopPropagation();
    
    // Запускаем анимацию подпрыгивания кнопки
    setIsButtonJumping(true);
    setTimeout(() => setIsButtonJumping(false), 300);
  };

  // Обработчик клика на контент попапа (чтобы избежать срабатывания от бэкдропа)
  const handleContentClick = (e: React.MouseEvent) => {
    e.stopPropagation();
  };

  return (
    <div className={styles.popupBackdrop} onClick={handleBackdropClick}>
      <div className={styles.popupContent} onClick={handleContentClick}>
        <h2 className={styles.popupTitle}>{country || "Ваша страна"} должна объединить всех!</h2>
        <div className={styles.gameStepsContainer}>
          <div className={styles.gameSteps}>
            <span className={styles.step}>
              <img src={click} alt="" />
              1. Кликай на страну, зарабатывай деньги
            </span>
            <span className={styles.arrow}>→</span>
            <span className={styles.step}>
              <img src={upgrade} alt="" />
              2. Трать деньги на улучшения в одной из сфер или на заработок денег
            </span>
            <span className={styles.arrow}>→</span>
            <span className={styles.step}>
              <img src={talent} alt="" />
              3. Исследуй таланты
            </span>
            <span className={styles.arrow}>→</span>
            <span className={styles.step}>
              <img src={project} alt="" />
              4. Некоторые таланты запускают проекты
            </span>
            <span className={styles.arrow}>→</span>
            <span className={styles.step}>
              <img src={problem} alt="" />
              5. Решай проблемы, чтобы проект не провалился
            </span>
            <span className={styles.arrow}>→</span>
            <span className={styles.step}>
              <img src={conquer} alt="" />
              6. Успей развиться или объединить всех, пока мы снова не подверглись атаке!
            </span>
          </div>
        </div>
        <animated.button 
          className={styles.okButton} 
          onClick={onClose}
          style={buttonAnimation}
        >
          ОК
        </animated.button>
      </div>
    </div>
  );
}; 