// Функция для получения доступных игр на текущем этапе
export const getAvailableGames = (gameState: GameState): ExtendedMiniGame[] => {
  return MINI_GAMES.filter(game => {
    // Если игра требует элемент из secondStage, проверяем его наличие
    if (game.requiredStageItem) {
      return gameState.secondStage.some(item => 
        typeof item === 'string' ? item === game.requiredStageItem : item.id === game.requiredStageItem
      );
    }
    // Если не требует - доступна всегда
    return true;
  });
};

// Функция для проверки, доступна ли игра (не на кулдауне)
export const isGameAvailable = (game: MiniGame, gameState: GameState): boolean => {
  const miniGameState = gameState.miniGamesState?.[game.id];
  if (!miniGameState?.lastUsed) return true;
  
  const now = Date.now();
  const cooldownMs = game.cooldown * 1000;
  return now - miniGameState.lastUsed >= cooldownMs;
};

// Функция для расчета оставшегося времени кулдауна в секундах
export const getRemainingCooldown = (game: MiniGame, gameState: GameState): number | null => {
  const miniGameState = gameState.miniGamesState?.[game.id];
  if (!miniGameState?.lastUsed) return null;
  
  const now = Date.now();
  const cooldownMs = game.cooldown * 1000;
  const elapsed = now - miniGameState.lastUsed;
  
  if (elapsed >= cooldownMs) return null;
  
  return Math.ceil((cooldownMs - elapsed) / 1000);
};

