import React from 'react';
import { useAppSelector, useAppDispatch } from '../../app/hooks';
import { selectedCountry, hideMinistryPopup } from '../../app/gameSlice';
import styles from './MinistryPopup.module.scss';

export const MinistryPopup: React.FC = () => {
  const dispatch = useAppDispatch();
  const country = useAppSelector(selectedCountry);

  const handleClose = () => {
    dispatch(hideMinistryPopup());
  };

  return (
    <div className={styles.popupBackdrop} onClick={handleClose}>
      <div className={styles.popupContent} onClick={(e) => e.stopPropagation()}>
        <div className={styles.header}>
          <h2>Министерство финансов сообщает:</h2>
        </div>
        <div className={styles.message}>
          <p>
            Добыча ресурсов кликая <strong>{country}</strong> еще никогда не была так прибыльна!
          </p>
        </div>
        <button className={styles.okButton} onClick={handleClose}>
          Понятно
        </button>
      </div>
    </div>
  );
}; 